@import "../../global.scss";

.showcase-container {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 2rem;

  .project-container {
    width: 100%;

    .project-carousel {
      height: 33rem;

      // Carousel dots depend on carousel item size, so make it bigger and actual content smaller to display dots outside of the content
      .carousel-item-container {
        height: 33rem;
        background-color: green;

        .carousel-title-content {
          align-items: center;
          background: linear-gradient(#116799, #1488cc);
          display: flex;
          height: 33rem;
          padding: 0 2.5rem;
          position: relative;

          .project-overview {
            display: flex;
            flex-direction: column;
            text-align: left;
            width: 95%;

            // Different phone image size depending on size of screen (this is for computer)
            @media screen and (min-width: 720px) {
              width: 40%; // Images takes other 50%
            }

            h2 {
              text-decoration: underline;
            }

            // Font for technologies used and the coming soon button
            h4 {
              align-items: center;
              color: $greyFontColor;
              display: flex;
              font-size: 1rem;
              margin-top: 1.25rem;

              span {
                background-color: white;
                display: flex;
                height: 0.05rem;
                margin: 0 0.5rem;
                width: 0.9rem;
              }
            }

            .technologies-used-container {
              display: flex;
              justify-content: space-evenly;
            }
          }
        }

        .carousel-item-content {
          align-items: center;
          background: linear-gradient(#1f252d, #24282d);
          display: flex;
          height: 33rem;
          justify-content: center;
          overflow: hidden;
          position: relative;

          .phone-screenshot {
            height: 14rem;
            object-fit: contain;
            position: absolute;

            // Different phone image size depending on size of screen (this is for computer)
            @media screen and (min-width: 720px) {
              height: 33rem;
            }

            &.diagonal-1 {
              transform-origin: center;
              transform: rotate(25deg);
            }
            &.diagonal-2 {
              transform-origin: center;
              transform: translateX(-8.5rem) rotate(25deg); // Translate first before rotate so that X goes exactly horizontal/vertical

              // Different phone image size depending on size of screen (this is for computer)
              @media screen and (min-width: 720px) {
                transform: translateX(-20rem) rotate(25deg);
              }
            }
            &.diagonal-3 {
              transform-origin: center;
              transform: translateX(10rem) translateY(-4rem) rotate(25deg); // Translate first before rotate so that X goes exactly horizontal/vertical

              // Inverse of diagonal-6
              @media screen and (min-width: 720px) {
                transform: translateX(29rem) translateY(22rem) rotate(25deg);
              }
            }

            &.diagonal-4 {
              transform-origin: center;
              transform: translateX(3.5rem) translateY(10rem) rotate(25deg);

              @media screen and (min-width: 720px) {
                transform: translateX(24rem) translateY(-10rem) rotate(25deg);
              }
            }
            &.diagonal-5 {
              transform-origin: center;
              transform: translateX(-2rem) translateY(-14rem) rotate(25deg);

              @media screen and (min-width: 720px) {
                transform: translateX(9.5rem) translateY(21rem) rotate(25deg);
              }
            }
            &.diagonal-6 {
              transform-origin: center;
              transform: translateX(-6.5rem) translateY(14rem) rotate(25deg);

              // Inverse of diagonal-3
              @media screen and (min-width: 720px) {
                transform: translateX(-29rem) translateY(-22rem) rotate(25deg);
              }
            }

            // Phone 1 of 2 display
            &.phone-1-2 {
              transform: translateX(-4rem) rotateY(15deg) skewY(-5deg);

              @media screen and (min-width: 720px) {
                transform: translateX(-10rem) rotateY(15deg) skewY(-5deg);
              }
            }

            // Phone 2 of 2 display
            &.phone-2-2 {
              transform: translateX(4rem) rotateY(15deg) skewY(5deg);

              @media screen and (min-width: 720px) {
                transform: translateX(10rem) rotateY(15deg) skewY(5deg);
              }
            }

            // Phone 1 of 3 display
            &.phone-1-3 {
            }

            // Phone 2 of 3 display
            &.phone-2-3 {
              transform: translateX(-7rem) rotateY(15deg) skewY(-5deg);

              @media screen and (min-width: 720px) {
                transform: translateX(-20rem) rotateY(15deg) skewY(-5deg);
              }
            }
            // Phone 3 of 3 display
            &.phone-3-3 {
              transform: translateX(7rem) rotateY(15deg) skewY(5deg);

              @media screen and (min-width: 720px) {
                transform: translateX(20rem) rotateY(15deg) skewY(5deg);
              }
            }
          }

          .carousel-item-description-container {
            align-items: flex-end;
            color: white;
            display: flex;
            height: 100%;
            justify-content: center;
            width: 100%;
            z-index: 5;

            .carousel-item-description {
              background-color: rgba($color: #000000, $alpha: 0.85);
              display: flex;
              flex: 1;
              font-size: 1rem;
              font-weight: 500;
              justify-content: center;
            }
          }
        }
      }
    }
  }
}
